	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import discussyisheng from '@/views/discussyisheng/list'
	import binglidangan from '@/views/binglidangan/list'
	import address from '@/views/address/list'
	import shangpinxinxi from '@/views/shangpinxinxi/list'
	import yuyuexinxi from '@/views/yuyuexinxi/list'
	import lingyangxinxi from '@/views/lingyangxinxi/list'
	import storeup from '@/views/storeup/list'
	import dongwuzhonglei from '@/views/dongwuzhonglei/list'
	import users from '@/views/users/list'
	import cart from '@/views/cart/list'
	import liulangdongwuxinxi from '@/views/liulangdongwuxinxi/list'
	import forum from '@/views/forum/list'
	import yonghu from '@/views/yonghu/list'
	import discussliulangdongwuxinxi from '@/views/discussliulangdongwuxinxi/list'
	import chat from '@/views/chat/list'
	import discussshangpinxinxi from '@/views/discussshangpinxinxi/list'
	import orders from '@/views/orders/list'
	import config from '@/views/config/list'
	import yisheng from '@/views/yisheng/list'
	import yishengRegister from '@/views/yisheng/register'
	import yishengCenter from '@/views/yisheng/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/yishengCenter',
			name: '',
			component: yishengCenter
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/discussyisheng',
			name: '',
			component: discussyisheng
		}
		,{
			path: '/binglidangan',
			name: '',
			component: binglidangan
		}
		,{
			path: '/address',
			name: '',
			component: address
		}
		,{
			path: '/shangpinxinxi',
			name: '',
			component: shangpinxinxi
		}
		,{
			path: '/yuyuexinxi',
			name: '',
			component: yuyuexinxi
		}
		,{
			path: '/lingyangxinxi',
			name: '',
			component: lingyangxinxi
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/dongwuzhonglei',
			name: '',
			component: dongwuzhonglei
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/cart',
			name: '',
			component: cart
		}
		,{
			path: '/liulangdongwuxinxi',
			name: '',
			component: liulangdongwuxinxi
		}
		,{
			path: '/forum',
			name: '',
			component: forum
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/discussliulangdongwuxinxi',
			name: '',
			component: discussliulangdongwuxinxi
		}
		,{
			path: '/chat',
			name: '',
			component: chat
		}
		,{
			path: '/discussshangpinxinxi',
			name: '',
			component: discussshangpinxinxi
		}
		,{
			path: '/orders',
			name: '',
			component: orders
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		,{
			path: '/yisheng',
			name: '',
			component: yisheng
		}
		]
	},
	{
		path: '/yishengRegister',
		name: '',
		component: yishengRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
